.App {
  padding: 36px;
  background: white;
}

.App-image {
  position: absolute;
  right: -180px;
  top: -480px;
  max-width: 1000px;
  width: auto;
  height: auto;
  animation: App-image-spin infinite 180s linear;
  pointer-events: none;
  z-index: 1;
  mix-blend-mode: normal
}

#shape1 {  
  position: absolute;
  right: 180px;
  top: 180px;
  /* z-index: 10; */
  width: 250px;
  height: 250px;
  animation: App-image-spin-counter infinite 120s linear;
  display: inline-block;
  mix-blend-mode: multiply;
  background-blend-mode: multiply;
  background-color: rgb(249, 212, 72);
}

#shape2 {  
  position: absolute;
  right: 500px;
  top: 120px;
  width: 50px;
  height: 250px;
  animation: App-image-spin-counter infinite 30s linear;
  display: inline-block;
  mix-blend-mode: multiply;
  background-blend-mode: multiply;
  background-color: rgb(249, 212, 72);
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

.App-header h2 {
  margin-bottom: 6px;
}

.App-header p {
  font-size: 24px;
  margin-top: 6px;
}

@keyframes App-image-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-image-spin-counter {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-180deg);
  }
}
